import React, { useEffect, useMemo } from 'react'
import { useForm } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import {
  vacancyFifthTabSchema,
  vacancyFirstTabSchema,
  vacancyFourthTabSchema,
  vacancySecondTabSchema,
  vacancyThirdTabSchema,
} from '../../../../utils/schemas'
import { useAppSelector } from '../../../../services/store/hooks'

const useTabsForm = (tab, open) => {
  const { vacancyToEdit } = useAppSelector((store) => store.vacanciesPage)
  const getResolver = (currentTab) => {
    switch (currentTab) {
      case 0:
        return yupResolver(vacancyFirstTabSchema)
      case 1:
        return yupResolver(vacancySecondTabSchema)
      case 2:
        return yupResolver(vacancyThirdTabSchema)
      case 3:
        return yupResolver(vacancyFourthTabSchema)
      case 4:
        return yupResolver(vacancyFifthTabSchema)
      default:
        return undefined
    }
  }
  const getFieldData = () =>
    vacancyToEdit
      ? {
          company_name: vacancyToEdit.company_name,
          website: vacancyToEdit.website,
          position: vacancyToEdit.position,
          courses: vacancyToEdit.courses,
          isSalaryNotRequired: !vacancyToEdit.salary && !vacancyToEdit.salary_max && !vacancyToEdit.currency,
          salary: vacancyToEdit.salary,
          salary_max: vacancyToEdit.salary_max,
          currency: vacancyToEdit.currency,
          isConfidentialLinkNotRequired: !vacancyToEdit.company_confidential_url,
          company_confidential_url: vacancyToEdit.company_confidential_url,
          cities: vacancyToEdit.cities,
          employment: vacancyToEdit.employment,
          design_formats: vacancyToEdit.design_formats,
          work_format: vacancyToEdit.work_format,
          experience_type: vacancyToEdit.experience_type,
          education_type: vacancyToEdit.education_type,
          isTestTaskLinkNotRequired: !vacancyToEdit.test_task_url,
          test_task_url: vacancyToEdit.test_task_url,
          vacancy_info: vacancyToEdit.vacancy_info,
          responsibilities: vacancyToEdit.responsibilities,
          circs: vacancyToEdit.circs,
          mandatory_requirements: vacancyToEdit.mandatory_requirements,
          optional_requirements: vacancyToEdit.optional_requirements,
          selection_stages: vacancyToEdit.selection_stages,
          chat_id: vacancyToEdit.chat_id,
          vacancy_url: vacancyToEdit.vacancy_url,
          company_logo: vacancyToEdit.company_logo || null,
          small_cover: vacancyToEdit.small_cover,
          big_cover: vacancyToEdit.big_cover,
        }
      : {
          company_logo: null,
        }
  const {
    control,
    handleSubmit,
    setValue,
    watch,
    register,
    trigger,
    getValues,
    setError,
    reset,
    formState: { errors },
  } = useForm({
    defaultValues: getFieldData(),
    mode: 'onChange',
    resolver: useMemo(() => getResolver(tab), [tab]),
  })

  useEffect(() => reset(getFieldData()), [vacancyToEdit?.company_name, open])

  const checkValidationError = () => {
    //       5 ,     
    if (
      tab === 4 &&
      (errors?.company_name?.message ||
        errors?.website?.message ||
        errors?.position?.message ||
        errors?.courses?.message ||
        errors?.salary?.message ||
        errors?.salary_max?.message ||
        errors?.currency?.message ||
        errors?.company_confidential_url?.message)
    )
      return '     '
    if (
      tab === 4 &&
      (errors?.cities?.message ||
        errors?.employment?.message ||
        errors?.design_formats?.message ||
        errors?.work_format?.message ||
        errors?.experience_type?.message ||
        errors?.education_type?.message ||
        errors?.test_task_url?.message)
    )
      return '    '
    if (tab === 4 && (errors?.vacancy_info?.message || errors?.responsibilities?.message || errors?.circs?.message))
      return '    '
    if (
      tab === 4 &&
      (errors?.mandatory_requirements?.message ||
        errors?.optional_requirements?.message ||
        errors?.design_formats?.message ||
        errors?.selection_stages?.message ||
        errors?.chat_id?.message)
    )
      return '      '
    //     
    if (tab === 4 && errors?.big_cover?.message && errors?.small_cover?.message) {
      return `   : ${errors?.big_cover?.message}.    : ${errors?.small_cover?.message}`
    }
    if (tab === 4 && errors?.big_cover?.message) {
      return `   : ${errors?.big_cover?.message}`
    }
    if (tab === 4 && errors?.small_cover?.message) {
      return `   : ${errors?.small_cover?.message}`
    }
    // if (tab === 5 && (errors?.company_logo?.message || errors?.small_cover?.message || errors?.big_cover?.message))
    //   return '    '
    return errors?.non_field_errors?.message
  }

  const validationError = checkValidationError()

  return {
    control,
    handleSubmit,
    setValue,
    watch,
    register,
    trigger,
    getValues,
    errors,
    setError,
    reset,
    validationError,
  }
}

export default useTabsForm
