import React, { useEffect, useState } from 'react'
import { Button, Typography } from '@mui/material'
import { Controller } from 'react-hook-form'
import useStyles from './VacancyPopup.styles'
import CheckboxItem from '../../../Sub/CheckboxGroup/CheckboxItem/CheckboxItem'
import { ControlledCompanyField, ControlledSelectField, ControlledTextField } from '../../../Sub/FormElements'

function FirstTabVacancy({ currencies, courses, setCurrentTab, formState, isEdit, handleClose, hookFormMethods }) {
  const [formData, setFormData] = formState
  const [salary, setSalary] = useState(undefined)
  const [salaryMax, setSalaryMax] = useState(undefined)
  const [confidentialUrl, setConfidentialUrl] = useState(undefined)
  const classes = useStyles()
  const { control, handleSubmit, setValue, watch, trigger, errors } = hookFormMethods

  const currencyValue = watch('currency')
  const company = watch('company_name')
  const isSalaryNotRequiredChecked = watch('isSalaryNotRequired')
  const isConfidentialLinkNotRequiredChecked = watch('isConfidentialLinkNotRequired')

  const handleCurrenciesChange = async (e) => {
    setValue('currency', e.target.value, { shouldValidate: true, shouldDirty: true })
  }

  const handleSalaryChange = async (e) => {
    setSalary(e.target.value)
    setValue('salary', e.target.value, { shouldValidate: true, shouldDirty: true })
    return !currencyValue ? setValue('currency', 4, { shouldValidate: true, shouldDirty: true }) : null
  }

  const handleSalaryMaxChange = async (e) => {
    setSalaryMax(e.target.value)
    setValue('salary_max', e.target.value, { shouldValidate: true, shouldDirty: true })
    await trigger('salary_max')
    await trigger('salary')
    return !currencyValue ? setValue('currency', 4, { shouldValidate: true, shouldDirty: false }) : null
  }

  const handleConfidentialUrlChange = (e) => {
    setConfidentialUrl(e.target.value)
    setValue('company_confidential_url', e.target.value, { shouldValidate: true, shouldDirty: true })
  }

  const onSubmit = (data) => {
    setFormData((prev) =>
      isEdit
        ? {
            ...prev,
            tabFirst: {
              ...data,
              salary:
                (data.salary === null && formData?.tabFirst?.salary) ||
                (formData?.tabFirst?.salary && isSalaryNotRequiredChecked)
                  ? null
                  : data.salary ?? undefined,
              salary_max:
                (data.salary_max === null && formData?.tabFirst?.salary_max) ||
                (formData?.tabFirst?.salary_max && isSalaryNotRequiredChecked)
                  ? null
                  : data.salary_max ?? undefined,
            },
          }
        : {
            ...prev,
            tabFirst: { ...data, salary: data.salary ?? undefined, salary_max: data.salary_max ?? undefined },
          },
    )
    setCurrentTab(1)
  }

  useEffect(() => {
    if (isSalaryNotRequiredChecked) {
      setSalary('')
      setSalaryMax('')
      setValue('salary', undefined, { shouldValidate: true, shouldDirty: true })
      setValue('salary_max', undefined, { shouldValidate: true, shouldDirty: true })
      setValue('currency', undefined, { shouldValidate: true, shouldDirty: true })
    }
  }, [isSalaryNotRequiredChecked])

  useEffect(() => {
    if (isConfidentialLinkNotRequiredChecked) {
      setConfidentialUrl('')
      setValue('company_confidential_url', undefined, { shouldValidate: true, shouldDirty: true })
    }
  }, [isConfidentialLinkNotRequiredChecked])

  useEffect(() => {
    if (formData?.tabFirst?.salary) {
      setSalary(formData?.tabFirst?.salary)
    }
  }, [formData?.tabFirst?.salary])

  useEffect(() => {
    if (formData?.tabFirst?.salary_max) {
      setSalaryMax(formData?.tabFirst?.salary_max)
    }
  }, [formData?.tabFirst?.salary_max])

  useEffect(() => {
    if (formData?.tabFirst?.company_confidential_url) {
      setConfidentialUrl(formData?.tabFirst?.company_confidential_url)
    }
  }, [formData?.tabFirst?.company_confidential_url])

  useEffect(() => {
    if (company === undefined) {
      setValue('website', '', { shouldValidate: true, shouldDirty: true })
      setValue('company_logo', null, { shouldValidate: true, shouldDirty: true })
      setFormData({
        ...formData,
        tabFifth: {
          ...formData.tabFifth,
          big_cover: null,
          small_cover: null,
        },
      })
      return
    }
    if (typeof company === 'object') {
      // setValue('company_name', company?.name, { shouldValidate: true, shouldDirty: true })
      if (company?.website) {
        setValue('website', company?.website, { shouldValidate: true, shouldDirty: true })
      }
      if (company?.company_logo) {
        setValue('company_logo', company?.company_logo, { shouldValidate: true, shouldDirty: true })
      }
      //     ,     
      //     -  
      if (company?.big_cover && company?.small_cover) {
        setValue('big_cover', company?.big_cover)
        setValue('small_cover', company?.small_cover)
        setFormData({
          ...formData,
          tabFifth: {
            ...formData.tabFifth,
            big_cover: company?.big_cover,
            small_cover: company?.small_cover,
          },
        })
      } else if (company?.big_cover) {
        setValue('big_cover', company?.big_cover)
        setFormData({
          ...formData,
          tabFifth: {
            ...formData.tabFifth,
            big_cover: company?.big_cover,
          },
        })
      } else if (company?.small_cover) {
        setValue('small_cover', company?.small_cover)
        setFormData({
          ...formData,
          tabFifth: {
            ...formData.tabFifth,
            small_cover: company?.small_cover,
          },
        })
      }
      //       
      setValue('company_name', company?.name, { shouldValidate: true, shouldDirty: true })
    }
  }, [company])

  return (
    <form className={classes.form} onSubmit={handleSubmit(onSubmit)}>
      <div className={classes.fieldsGrid}>
        <ControlledCompanyField
          control={control}
          error={!!errors?.company_name?.message}
          helperText={errors?.company_name?.message}
        />
        <ControlledTextField
          labelTitle=""
          fieldName="website"
          control={control}
          error={!!errors.website?.message}
          helperText={errors.website?.message}
          placeholder="  "
        />
        <ControlledTextField
          labelTitle=""
          fieldName="position"
          control={control}
          error={!!errors.position?.message}
          helperText={errors.position?.message}
          placeholder="  "
        />
        <Controller
          name="courses"
          control={control}
          render={({ field: { name, value, onChange } }) => (
            <ControlledSelectField
              labelTitle=""
              tipContent="  "
              name={name}
              value={value || []}
              onChange={onChange}
              error={!!errors?.courses?.message}
              helperText={errors?.courses?.message}
              items={courses}
              displayEmpty
              multiple
              maxWidth={306}
            />
          )}
        />
        <div className={classes.salaryContainer}>
          <Typography className={classes.salaryLabel} variant="body2">
            
          </Typography>
          <div className={classes.salaryInputContainer}>
            <ControlledTextField
              fieldName="salary"
              control={control}
              value={salary}
              error={!!errors.salary?.message}
              helperText={errors.salary?.message}
              placeholder=""
              onChange={handleSalaryChange}
              maxWidth={148}
              disabled={isSalaryNotRequiredChecked}
            />
            <ControlledTextField
              fieldName="salary_max"
              control={control}
              value={salaryMax}
              error={!!errors.salary_max?.message}
              helperText={errors.salary_max?.message}
              placeholder=""
              onChange={handleSalaryMaxChange}
              maxWidth={148}
              disabled={isSalaryNotRequiredChecked}
            />
          </div>
        </div>
        <div className={classes.currencyContainer}>
          <ControlledSelectField
            labelTitle=""
            tipContent=" "
            value={+currencyValue}
            error={!!errors?.currency?.message}
            helperText={errors?.currency?.message}
            onChange={handleCurrenciesChange}
            items={currencies}
            maxWidth="148px"
            disabled={isSalaryNotRequiredChecked}
          />
          <Controller
            name="isSalaryNotRequired"
            control={control}
            render={({ field }) => (
              <CheckboxItem
                {...field}
                checkboxLabel=" "
                checked={isSalaryNotRequiredChecked}
                margin={{ marginBottom: 0 }}
              />
            )}
          />
        </div>
        <ControlledTextField
          labelTitle="  "
          fieldName="company_confidential_url"
          value={confidentialUrl}
          onChange={handleConfidentialUrlChange}
          control={control}
          error={!!errors.company_confidential_url?.message}
          helperText={errors.company_confidential_url?.message}
          disabled={isConfidentialLinkNotRequiredChecked}
          placeholder="   "
        />
        <Controller
          name="isConfidentialLinkNotRequired"
          control={control}
          render={({ field }) => (
            <CheckboxItem
              {...field}
              checkboxLabel=" "
              checked={isConfidentialLinkNotRequiredChecked}
              margin={{ marginBottom: 0 }}
            />
          )}
        />
      </div>
      {isEdit ? (
        <div className={classes.buttonContainer}>
          <Button type="button" className={classes.previousButton} onClick={handleClose}>
            
          </Button>
          <Button className={classes.submitButton} type="submit">
            
          </Button>
        </div>
      ) : (
        <Button className={classes.submitButton} type="submit" style={{ width: 306 }}>
          
        </Button>
      )}
    </form>
  )
}

export default React.memo(FirstTabVacancy)
