import React, { useMemo, useEffect, useState, FC } from 'react'
import debounce from 'lodash.debounce'
import { useForm } from 'react-hook-form'
import {
  Box,
  Button,
  Dialog,
  DialogContent,
  DialogTitle,
  IconButton,
  Stack,
  ThemeProvider,
  Typography,
} from '@mui/material'
import { DataGrid, GridRowsProp } from '@mui/x-data-grid'
import { AxiosError } from 'axios'
import LoadingOverlay from '../../../Sub/DataTable/LoadingOverlay/LoadingOverlay'
import TextFieldSearch from '../../../Sub/Filters/textfield-search/textfield-search'
import useStyles from './VacancyInfoPopup.styles'
import theme from '../../../../utils/themeMUI5'
import IconClose from '../../../../assets/Icons/IconClose/IconClose'
import { titleIDs, getInfoColumns } from './columns'
import useAsync from '../../../../hooks/useAsync'
import api from '../../../../api/api'
import { API_HR_PART_EXPORT_INFO_ENDPOINT } from '../../../../utils/constants'
import {
  IStudentStatus,
  TDataBlobError,
  TDownloadFile,
  TGetCellClassName,
  TGetRowHeight,
} from '../../../../services/types/vacacies-page-types/vacancies-page-types'
import {
  setIsInfoPopupOpen,
  setIsOpenFoundJobPopup,
  setIsOpenVacancyPopup,
  setStudentToEdit,
  setVacancyToEdit,
} from '../../../../services/slices/vacancies-page-slice'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import { getInfo, patchStudentStatus } from '../../../../services/axios/vacancies-page'

const getRowHeight: TGetRowHeight = ({ id }) => (titleIDs.includes(id.id) ? 32 : 64)

const getCellClassName: TGetCellClassName = ({ row, field }) => {
  if (titleIDs.includes(row.id as string)) {
    if (field === 'id') {
      return 'title'
    }
  }
  return ''
}

const VacancyInfoPopup = () => {
  const dispatch = useAppDispatch()
  const { directoriesData, isInfoPopupOpen, vacancyToEdit, isLoading, isOpenFoundJobPopup, rows } = useAppSelector(
    (store) => store.vacanciesPage,
  )

  const [student, setStudent] = useState('')
  const [searchQuery, setSearchQuery] = useState('')
  const classes = useStyles()

  const { control } = useForm()

  const { run: runDownload } = useAsync({
    data: [],
    error: null,
  })

  const handleClose = () => {
    dispatch(setIsInfoPopupOpen(false))
    dispatch(setVacancyToEdit(null))
  }

  const handleEditClick = () => {
    dispatch(setIsOpenVacancyPopup(true))
  }

  const patchStudentStatusOnPopup = async (studentId: number, status: IStudentStatus) => {
    try {
      await dispatch(patchStudentStatus({ status, studentId }))
    } catch {
      return undefined
    }
    return undefined
  }

  const patchStatus = (studentId: number, status: IStudentStatus) => {
    if (status.status === ' ') {
      dispatch(setStudentToEdit({ id: studentId }))
      //      
      dispatch(setIsInfoPopupOpen(false))
      dispatch(setIsOpenFoundJobPopup(true))
      return
    }
    patchStudentStatusOnPopup(studentId, status)
  }

  const columns = getInfoColumns(directoriesData?.studentStatuses, patchStatus)

  const getInfoOnPopup = async () => {
    if (vacancyToEdit) {
      try {
        await dispatch(getInfo({ searchQuery, id: vacancyToEdit.id }))
      } catch {
        return undefined
      }
    }
    return undefined
  }

  const downloadFile: TDownloadFile = async (apiUrl, type, filename) => {
    try {
      const response = await api.get(apiUrl, { responseType: 'blob' })
      const blob = new Blob([response?.data], { type })
      const url = window.URL.createObjectURL(blob)
      const link = document.createElement('a')
      link.href = url
      link.setAttribute('download', filename)
      document.body.appendChild(link)
      link.click()
      return Promise.resolve(response?.data)
    } catch (e) {
      const err = e as AxiosError
      const isJsonBlob = (data: TDataBlobError) => data instanceof Blob && data.type === 'application/json'
      const responseData = isJsonBlob(err.response?.data) ? await err.response?.data?.text() : err.response?.data || {}
      const responseJson = typeof responseData === 'string' ? JSON.parse(responseData) : responseData
      return Promise.reject(responseJson)
    }
  }

  const exportFile = () => {
    if (vacancyToEdit)
      runDownload(
        downloadFile(
          API_HR_PART_EXPORT_INFO_ENDPOINT(vacancyToEdit.id),
          'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
          'export.xlsx',
        ),
      )
  }

  const changeSearchQuery = (v: string) => {
    if (v === '' || v.length > 2) setSearchQuery(v)
  }

  const debouncedChangeSearchQuery = useMemo(() => debounce(changeSearchQuery, 300), [])

  useEffect(() => () => debouncedChangeSearchQuery.cancel(), [])

  useEffect(() => {
    debouncedChangeSearchQuery(student)
  }, [student])

  useEffect(() => {
    if (isInfoPopupOpen) getInfoOnPopup()
  }, [isInfoPopupOpen, searchQuery, isOpenFoundJobPopup])

  return (
    <ThemeProvider theme={theme}>
      <Dialog
        open={isInfoPopupOpen}
        onClose={handleClose}
        sx={{ '& .MuiPaper-root': { width: 712, maxWidth: 712, height: 636, maxHeight: 636, overflow: 'hidden' } }}
      >
        {/* eslint-disable-next-line @typescript-eslint/ban-ts-comment */}
        {/* @ts-ignore */}
        <IconButton onClick={handleClose} variant="close-popup">
          <IconClose />
        </IconButton>
        <DialogTitle component="div">
          <div className={classes.header}>
            <div className={classes.info}>
              <Typography variant="h2" component="h2">
                  
              </Typography>
              <p className={classes.subheader}>
                <Typography variant="body" className={classes.company}>
                  {vacancyToEdit?.company_name}
                </Typography>
                <Typography variant="body" className={classes.company}>
                  {vacancyToEdit?.position}
                </Typography>
              </p>
            </div>
            <Button color="primary" sx={{ width: 141 }} onClick={handleEditClick}>
              
            </Button>
          </div>
          <div className={classes.header} style={{ marginTop: 26 }}>
            <TextFieldSearch
              value={student}
              control={control}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => setStudent(e?.target?.value)}
              placeholder=" "
              error={undefined}
              helperText={undefined}
            />
            <Button color="secondary" sx={{ width: 169 }} onClick={exportFile}>
               
            </Button>
          </div>
        </DialogTitle>
        <DialogContent>
          <Box
            sx={{
              height: 460,
              width: '100%',
              marginTop: '14px',
              '& .title': {
                fontSize: 13,
                fontWeight: 400,
                lineHeight: '16px',
                padding: '0 !important',
                justifyContent: 'center !important',
                alignItems: 'center !important',
                backgroundColor: '#F1F6FF',
              },
            }}
          >
            <DataGrid
              rows={rows as GridRowsProp}
              columns={columns}
              pageSize={100}
              rowsPerPageOptions={[5]}
              disableSelectionOnClick
              disableColumnMenu
              disableColumnSelector
              getRowHeight={(id) => getRowHeight({ id })}
              getCellClassName={getCellClassName}
              loading={isLoading}
              components={{
                NoRowsOverlay: () => (
                  <Stack height="100%" alignItems="center" justifyContent="center">
                     
                  </Stack>
                ),
                NoResultsOverlay: () => (
                  <Stack height="100%" alignItems="center" justifyContent="center">
                    Local filter returns no result
                  </Stack>
                ),
                ErrorOverlay: () => (
                  <Stack height="100%" alignItems="center" justifyContent="center">
                    Error
                  </Stack>
                ),
                LoadingOverlay,
              }}
            />
          </Box>
        </DialogContent>
      </Dialog>
    </ThemeProvider>
  )
}

export default React.memo(VacancyInfoPopup)
