import React, { useEffect, useState } from 'react'
import Button from '@material-ui/core/Button'
import useStyles from './VacancyPopup.styles'
import ControlledTextField from '../../../Sub/FormElements/controlled-text-field/controlled-text-field'

function FourthTabVacancy({ setCurrentTab, formState, hookFormMethods }) {
  const [formData, setFormData] = formState
  const classes = useStyles()
  const [requiredTextCount, setRequiredTextCount] = useState(0)
  const [notRequiredTextCount, setNotRequiredTextCount] = useState(0)
  const [stageCount, setStageCount] = useState(0)

  const { control, register, watch, handleSubmit, getValues, errors } = hookFormMethods

  const requiredValue = watch('mandatory_requirements')
  const notRequiredValue = watch('optional_requirements')
  const stageValue = watch('selection_stages')

  useEffect(() => {
    register('mandatory_requirements')
    register('optional_requirements')
    register('selection_stages')
  }, [register])

  useEffect(() => {
    setRequiredTextCount(requiredValue ? requiredValue.length : 0)
  }, [requiredValue])

  useEffect(() => {
    setNotRequiredTextCount(notRequiredValue ? notRequiredValue.length : 0)
  }, [notRequiredValue])

  useEffect(() => {
    setStageCount(stageValue ? stageValue.length : 0)
  }, [stageValue])

  const handlePreviousClick = () => {
    const values = getValues()
    setFormData((prev) => ({ ...prev, tabFourth: values }))
    setCurrentTab(2)
  }

  const onSubmit = (data) => {
    setFormData((prev) => ({ ...prev, tabFourth: data }))
    setCurrentTab(4)
  }

  return (
    <form style={{ width: '632px' }} className={classes.form} onSubmit={handleSubmit(onSubmit)}>
      <div className={classes.fieldsGridFlex}>
        <div className={classes.fieldsContainer}>
          <ControlledTextField
            labelTitle=" "
            fieldName="mandatory_requirements"
            control={control}
            error={!!errors.mandatory_requirements?.message}
            helperText={errors.mandatory_requirements?.message}
            placeholder=" "
            multiline
            rows={4}
            symbolsCount={requiredTextCount}
            maxLength={1000}
            minLength={20}
            maxWidth={306}
            textClass={classes.textAreaSmaller}
          />
          <ControlledTextField
            labelTitle=" "
            fieldName="optional_requirements"
            control={control}
            error={!!errors.optional_requirements?.message}
            helperText={errors.optional_requirements?.message}
            placeholder=" "
            multiline
            rows={4}
            symbolsCount={notRequiredTextCount}
            maxLength={1000}
            maxWidth={306}
            textClass={classes.textAreaSmaller}
          />
        </div>
        <div className={classes.fieldsContainer}>
          <ControlledTextField
            labelTitle=" "
            fieldName="selection_stages"
            control={control}
            error={!!errors.selection_stages?.message}
            helperText={errors.selection_stages?.message}
            placeholder=" "
            multiline
            rows={4}
            symbolsCount={stageCount}
            maxLength={500}
            minLength={1}
            maxWidth={306}
            textClass={classes.textAreaVerySmall}
          />
          <div className={classes.fieldLinkContainer}>
            <ControlledTextField
              labelTitle=" ID,   "
              fieldName="chat_id"
              control={control}
              error={!!errors.chat_id?.message}
              helperText={errors.chat_id?.message}
              placeholder=", 2427391205001  -129231"
            />
            <ControlledTextField
              labelTitle="  "
              fieldName="vacancy_url"
              control={control}
              error={!!errors.vacancy_url?.message}
              helperText={errors.vacancy_url?.message}
              placeholder=" "
            />
          </div>
        </div>
      </div>
      <div className={classes.buttonContainer}>
        <Button type="button" className={classes.previousButton} onClick={handlePreviousClick}>
          
        </Button>
        <Button className={classes.submitButton} type="submit">
          
        </Button>
      </div>
    </form>
  )
}

export default React.memo(FourthTabVacancy)
