import React, { useEffect, useState } from 'react'
import isEqual from 'lodash.isequal'
import { FormHelperText } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import { ThemeProvider, Typography, Box } from '@mui/material'
import useStyles from './VacancyPopup.styles'
import api from '../../../../api/api'
import useAsync from '../../../../hooks/useAsync'
import {
  API_HR_PART_VACANCY_ENDPOINT,
  API_HR_PART_VACANCY_BY_ID_ENDPOINT,
  SUPPORTED_FORMATS,
  COVERS_EXAMPLE_LINK,
} from '../../../../utils/constants'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import { setVacancyToEdit } from '../../../../services/slices/vacancies-page-slice'
import noLogo from '../../../../assets/noLogoIcon.svg'
import theme from '../../../../utils/themeMUI5'
import Dropzone from '../../../Sub/Dropzone/Dropzone'
import ControlledLogoField from '../../../Sub/FormElements/controlled-logo-field/controlled-logo-field'
import getFormData from '../../../../utils/object-to-form-data'
import processServerError from '../../../../utils/server-error'

function FifthTabVacancy({
  setCurrentTab,
  formState,
  setSnack,
  handleClose,
  saveButtonTitle,
  hookFormMethods,
  validationError,
}) {
  const [formData, setFormData] = formState
  const [logo, setLogo] = useState(noLogo)
  const dispatch = useAppDispatch()
  const { vacancyToEdit } = useAppSelector((store) => store.vacanciesPage)
  const classes = useStyles()
  const [bigCover, setBigCover] = useState([])
  const [cover, setCover] = useState([])

  const { run } = useAsync({
    data: null,
    error: null,
  })

  const { setValue, handleSubmit, getValues, register, setError, watch, errors } = hookFormMethods
  useEffect(() => {
    register('company_logo')
    register('big_cover')
    register('small_cover')
  }, [])

  const companyLogo = watch('company_logo')

  useEffect(() => {
    setValue('small_cover', Array.isArray(cover) ? cover[0] : cover)
  }, [cover])

  useEffect(() => {
    setValue('big_cover', Array.isArray(bigCover) ? bigCover[0] : bigCover)
  }, [bigCover])

  useEffect(() => {
    if (formData?.tabFifth?.small_cover) setCover(formData?.tabFifth?.small_cover)
    if (formData?.tabFifth?.big_cover) setBigCover(formData?.tabFifth?.big_cover)
  }, [])

  useEffect(() => {
    const newLogo = companyLogo instanceof File ? URL.createObjectURL(companyLogo) : companyLogo
    setLogo(newLogo)
  }, [companyLogo])

  const handleImageChange = (e) => {
    setValue('company_logo', e.target.files[0], { shouldValidate: true, shouldDirty: false })
  }

  const handlePreviousClick = () => {
    const values = getValues()
    setFormData((prev) => ({ ...prev, tabFifth: values }))
    setCurrentTab(3)
  }

  const postForm = async (data) => {
    try {
      const response = await api.post(API_HR_PART_VACANCY_ENDPOINT, data, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      })
      if (response.status === 201) {
        handleClose()
        setError(null)
        setSnack({
          open: true,
          header: ' ',
          message: '  ',
        })
      }
      return Promise.resolve(response.data)
    } catch (e) {
      return processServerError(e, setError)
    }
  }

  const patchForm = async (data) => {
    try {
      const response = await api.patch(API_HR_PART_VACANCY_BY_ID_ENDPOINT(vacancyToEdit.id), data, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      })
      if (response.status === 200) {
        dispatch(setVacancyToEdit(response.data))
        handleClose()
        setError(null)
        setSnack({
          open: true,
          header: ' ',
          message: '  ',
        })
      }
      return Promise.resolve(response.data)
    } catch (e) {
      return processServerError(e, setError)
    }
  }

  const onSubmit = (fifthTabData) => {
    const data = vacancyToEdit
      ? {
          ...formData?.tabFirst,
          currency: formData?.tabFirst?.currency && +formData?.tabFirst?.currency,
          ...formData?.tabSecond,
          ...formData?.tabThird,
          ...formData?.tabFourth,
          isSalaryNotRequired: undefined,
          isTestTaskLinkNotRequired: undefined,
          isConfidentialLinkNotRequired: undefined,
          cities: formData?.tabSecond?.cities?.map((i) => i?.id) ?? [],
          website: formData?.tabFirst?.website || undefined,
          company_logo: fifthTabData?.company_logo,
          big_cover: fifthTabData?.big_cover,
          small_cover: fifthTabData?.small_cover,
        }
      : {
          ...formData?.tabFirst,
          currency: formData?.tabFirst?.currency && +formData?.tabFirst?.currency,
          ...formData?.tabSecond,
          ...formData?.tabThird,
          ...formData?.tabFourth,
          isSalaryNotRequired: undefined,
          isTestTaskLinkNotRequired: undefined,
          isConfidentialLinkNotRequired: undefined,
          cities: formData?.tabSecond?.cities?.map((i) => i?.id) ?? [],
          website: formData?.tabFirst?.website || undefined,
          //    ,       
          company_logo: typeof fifthTabData?.company_logo === 'string' ? undefined : fifthTabData?.company_logo,
          big_cover: typeof fifthTabData?.big_cover === 'string' ? undefined : fifthTabData?.big_cover,
          small_cover: typeof fifthTabData?.small_cover === 'string' ? undefined : fifthTabData?.small_cover,
        }

    const prepareData = (array) => {
      const {
        work_format,
        cities,
        courses,
        design_formats,
        education_type,
        employment,
        experience_type,
        big_cover,
        small_cover,
      } = array
      //  ,   
      array.work_format = null
      array.cities = null
      array.courses = null
      array.design_formats = null
      array.education_type = null
      array.employment = null
      array.experience_type = null
      // array.covers = null
      const fd = getFormData(array)
      if (work_format) work_format.forEach((item) => fd.append('work_format', item))
      if (cities) cities.forEach((item) => fd.append('cities', item))
      if (courses) courses.forEach((item) => fd.append('courses', item))
      if (design_formats) design_formats.forEach((item) => fd.append('design_formats', item))
      if (education_type) education_type.forEach((item) => fd.append('education_type', item))
      if (employment) employment.forEach((item) => fd.append('employment', item))
      if (experience_type) experience_type.forEach((item) => fd.append('experience_type', item))
      //  ,    null   
      if (big_cover === null && vacancyToEdit) fd.append('big_cover', '')
      if (small_cover === null && vacancyToEdit) fd.append('small_cover', '')
      return fd
    }

    if (!vacancyToEdit) return run(postForm(prepareData(data)))

    let isDataUpdated = false

    const isFieldChanged = (fieldName) => {
      if (fieldName === 'cities') {
        const vacanciesCitiesID = vacancyToEdit[fieldName]?.map((city) => city?.id)
        const isChanged = !isEqual(data[fieldName], vacanciesCitiesID ?? undefined)
        if (isChanged) isDataUpdated = true
        return isChanged
      }
      //    null,  null ?? undefined  undefined
      if (fieldName === 'company_logo') {
        const isChanged = data && !isEqual(data[fieldName], vacancyToEdit[fieldName])
        if (isChanged) isDataUpdated = true
        return isChanged
      }
      //  .    ,    
      if (fieldName === 'small_cover') {
        if (data.company_name === vacancyToEdit.company_name) {
          const isChanged = data && !isEqual(data[fieldName], vacancyToEdit[fieldName])
          if (isChanged) isDataUpdated = true
          return isChanged
        }
        //   ,   ,   false
        if (formData?.tabFifth?.small_cover === data.small_cover) {
          return false
        }
        //  null  undefined
        if (formData?.tabFifth?.small_cover === null && data.small_cover === undefined) {
          return false
        }
        //   
        isDataUpdated = true
        return true
      }
      if (fieldName === 'big_cover') {
        if (data.company_name === vacancyToEdit.company_name) {
          const isChanged = data && !isEqual(data[fieldName], vacancyToEdit[fieldName])
          if (isChanged) isDataUpdated = true
          return isChanged
        }
        //   ,   ,   false
        if (formData?.tabFifth?.big_cover === data.big_cover) {
          return false
        }
        //  null  undefined
        if (formData?.tabFifth?.big_cover === null && data.big_cover === undefined) {
          return false
        }
        //   
        isDataUpdated = true
        return true
      }
      const isChanged =
        fieldName === 'vacancy_url'
          ? data && data[fieldName] !== vacancyToEdit[fieldName]
          : data && !isEqual(data[fieldName], vacancyToEdit[fieldName] ?? undefined)
      if (isChanged) isDataUpdated = true
      return isChanged
    }

    const patchData = {
      company_name: isFieldChanged('company_name') ? data.company_name : undefined,
      website: isFieldChanged('website') ? data.website || null : undefined,
      position: isFieldChanged('position') ? data.position : undefined,
      courses: isFieldChanged('courses') ? data.courses : undefined,
      salary: isFieldChanged('salary') ? data.salary : undefined,
      salary_max: isFieldChanged('salary_max') ? data.salary_max : undefined,
      currency: isFieldChanged('currency') ? data.currency : undefined,
      company_confidential_url: isFieldChanged('company_confidential_url') ? data.company_confidential_url : undefined,
      cities: isFieldChanged('cities') ? data.cities : undefined,
      employment: isFieldChanged('employment') ? data.employment : undefined,
      design_formats: isFieldChanged('design_formats') ? data.design_formats : undefined,
      work_format: isFieldChanged('work_format') ? data.work_format : undefined,
      experience_type: isFieldChanged('experience_type') ? data.experience_type : undefined,
      education_type: isFieldChanged('education_type') ? data.education_type : undefined,
      test_task_url: isFieldChanged('test_task_url') ? data.test_task_url : undefined,
      vacancy_info: isFieldChanged('vacancy_info') ? data.vacancy_info : undefined,
      responsibilities: isFieldChanged('responsibilities') ? data.responsibilities : undefined,
      circs: isFieldChanged('circs') ? data.circs : undefined,
      mandatory_requirements: isFieldChanged('mandatory_requirements') ? data.mandatory_requirements : undefined,
      optional_requirements: isFieldChanged('optional_requirements') ? data.optional_requirements : undefined,
      selection_stages: isFieldChanged('selection_stages') ? data.selection_stages : undefined,
      chat_id: isFieldChanged('chat_id') ? data.chat_id : undefined,
      vacancy_url: isFieldChanged('vacancy_url') ? data.vacancy_url : undefined,
      company_logo: isFieldChanged('company_logo') ? data.company_logo : undefined,
      //    null  
      small_cover: isFieldChanged('small_cover') ? data.small_cover ?? null : undefined,
      big_cover: isFieldChanged('big_cover') ? data.big_cover ?? null : undefined,
    }

    if (vacancyToEdit.salary && formData.tabFirst.isSalaryNotRequired) {
      patchData.salary = null
      patchData.salary_max = null
      patchData.currency = null
    }

    if (vacancyToEdit.company_confidential_url && formData.tabFirst.isConfidentialLinkNotRequired) {
      patchData.company_confidential_url = null
    }

    if (vacancyToEdit.test_task_url && formData.tabSecond.isTestTaskLinkNotRequired) {
      patchData.test_task_url = null
    }
    if (isDataUpdated) return run(patchForm(prepareData(patchData)))

    handleClose()
    return setSnack({
      open: true,
      header: '  ',
      message: '     ',
    })
  }

  return (
    <ThemeProvider theme={theme}>
      <form style={{ width: '632px' }} className={classes.form} onSubmit={handleSubmit(onSubmit)}>
        <ControlledLogoField avatar={logo} helperText={errors?.company_logo?.message} onChange={handleImageChange} />
        <Box
          mt="-11px"
          mb="12px"
          sx={{ display: 'flex', gap: '24px', width: '100%', minHeight: '176px', boxSizing: 'border-box' }}
        >
          <Box sx={{ display: 'flex', flexDirection: 'column', boxSizing: 'border-box' }}>
            <Dropzone
              filesState={[bigCover, setBigCover]}
              maxFilesNumber={1}
              label="  "
              nameTip=""
              fileTip=" : .png, .jpg,   10 .  : 1280  1024"
              error={errors?.big_cover?.message}
              isBigCover
              acceptedFormats={SUPPORTED_FORMATS}
              isVacancyPopup
            />
          </Box>
          <Box sx={{ display: 'flex', flexDirection: 'column', boxSizing: 'border-box' }}>
            <Dropzone
              filesState={[cover, setCover]}
              maxFilesNumber={1}
              label="  "
              nameTip=""
              fileTip=" : .png, .jpg,   10 .  : 320  400"
              error={errors?.small_cover?.message}
              isCover
              acceptedFormats={SUPPORTED_FORMATS}
              isVacancyPopup
            />
          </Box>
        </Box>
        <Typography mt="-52px" sx={{ alignSelf: 'flex-start' }} variant="body" color={theme.color.black500}>
          {' '}
          <a
            style={{ color: theme.color.blueMain, textDecoration: 'none' }}
            href={COVERS_EXAMPLE_LINK}
            target="_blank"
            rel="noreferrer"
            
          >
             
          </a>
        </Typography>
        <FormHelperText className={classes.formHelper}>{validationError || ' '}</FormHelperText>
        <div className={classes.buttonContainer}>
          <Button type="button" className={classes.previousButton} onClick={handlePreviousClick}>
            
          </Button>
          <Button className={classes.submitButton} type="submit">
            {saveButtonTitle}
          </Button>
        </div>
      </form>
    </ThemeProvider>
  )
}

export default React.memo(FifthTabVacancy)
