/* eslint-disable no-unused-vars */
import { Button } from '@mui/material'
import React, { useEffect, useState } from 'react'
import { useForm, Controller } from 'react-hook-form'
import { ControlledSelectField } from '../../../Sub/FormElements'
import useStyles from './VacancyPopup.styles'
import ControlledTextField from '../../../Sub/FormElements/controlled-text-field/controlled-text-field'
import CheckboxItem from '../../../Sub/CheckboxGroup/CheckboxItem/CheckboxItem'
import ControlledMultiCityField from '../../../Sub/FormElements/controlled-multi-city-field/controlled-multi-city-field'

function SecondTabVacancy({ directories, setCurrentTab, formState, hookFormMethods }) {
  const [formData, setFormData] = formState
  const [testTaskUrl, setTestTasklUrl] = useState(undefined)
  const classes = useStyles()
  const { control, handleSubmit, getValues, watch, setValue, errors } = hookFormMethods
  const isTestTaskLinkNotRequiredChecked = watch('isTestTaskLinkNotRequired')

  const handleTestTaskUrlChange = (e) => {
    setTestTasklUrl(e.target.value)
    setValue('test_task_url', e.target.value, { shouldValidate: true, shouldDirty: true })
  }

  useEffect(() => {
    if (isTestTaskLinkNotRequiredChecked) {
      setTestTasklUrl('')
      setValue('test_task_url', undefined, { shouldValidate: true, shouldDirty: true })
    }
  }, [isTestTaskLinkNotRequiredChecked])

  useEffect(() => {
    if (formData?.tabSecond?.test_task_url) {
      setTestTasklUrl(formData?.tabSecond?.test_task_url)
    }
  }, [formData?.tabSecond?.test_task_url])

  const handlePreviousClick = () => {
    const values = getValues()
    setFormData((prev) => ({ ...prev, tabSecond: values }))
    setCurrentTab(0)
  }
  const onSubmit = (data, e) => {
    setFormData((prev) => ({ ...prev, tabSecond: data }))
    setCurrentTab(2)
  }

  return (
    <form className={classes.form} onSubmit={handleSubmit(onSubmit)}>
      <div className={classes.fieldsGrid} style={{ gridRowGap: 10 }}>
        <div style={{ gridColumn: '1 / 3' }}>
          <ControlledMultiCityField
            control={control}
            error={!!errors?.cities?.message}
            helperText={errors?.cities?.message}
          />
        </div>
        <Controller
          name="work_format"
          control={control}
          render={({ field: { name, value, onChange } }) => (
            <ControlledSelectField
              labelTitle=" "
              tipContent="  "
              name={name}
              value={value || []}
              onChange={onChange}
              error={!!errors?.work_format?.message}
              helperText={errors?.work_format?.message}
              items={directories?.workFormatItems}
              displayEmpty
              multiple
              selectAll=" "
              maxWidth={306}
            />
          )}
        />
        <Controller
          name="design_formats"
          control={control}
          render={({ field: { name, value, onChange } }) => (
            <ControlledSelectField
              labelTitle=" "
              tipContent="  "
              name={name}
              value={value || []}
              onChange={onChange}
              error={!!errors?.design_formats?.message}
              helperText={errors?.design_formats?.message}
              items={directories.jobApplicationItems}
              displayEmpty
              multiple
              selectAll=" "
              maxWidth={306}
            />
          )}
        />
        <Controller
          name="experience_type"
          control={control}
          render={({ field: { name, value, onChange } }) => (
            <ControlledSelectField
              labelTitle="      ?"
              tipContent="  "
              name={name}
              value={value || []}
              onChange={onChange}
              error={!!errors?.experience_type?.message}
              helperText={errors?.experience_type?.message}
              items={directories?.experienceItems}
              displayEmpty
              multiple
              selectAll=" "
              maxWidth={306}
            />
          )}
        />
        <Controller
          name="education_type"
          control={control}
          render={({ field: { name, value, onChange } }) => (
            <ControlledSelectField
              labelTitle="       ?"
              tipContent="  "
              name={name}
              value={value || []}
              onChange={onChange}
              error={!!errors?.education_type?.message}
              helperText={errors?.education_type?.message}
              items={directories?.educationItems}
              displayEmpty
              multiple
              selectAll=" "
              maxWidth={306}
            />
          )}
        />
        <div>
          <ControlledTextField
            labelTitle="   "
            fieldName="test_task_url"
            value={testTaskUrl}
            onChange={handleTestTaskUrlChange}
            control={control}
            error={!!errors.test_task_url?.message}
            helperText={errors.test_task_url?.message}
            disabled={isTestTaskLinkNotRequiredChecked}
            placeholder="   "
          />
          <Controller
            name="isTestTaskLinkNotRequired"
            control={control}
            render={({ field }) => (
              <CheckboxItem
                {...field}
                checkboxLabel="     "
                checked={isTestTaskLinkNotRequiredChecked}
                margin={{ marginBottom: 0 }}
              />
            )}
          />
        </div>
        <Controller
          name="employment"
          control={control}
          render={({ field: { name, value, onChange } }) => (
            <ControlledSelectField
              labelTitle=""
              tipContent="  "
              name={name}
              value={value || []}
              onChange={onChange}
              error={!!errors?.employment?.message}
              helperText={errors?.employment?.message}
              items={directories?.employmentItems}
              displayEmpty
              multiple
              selectAll=" "
              maxWidth={306}
            />
          )}
        />
      </div>
      <div className={classes.buttonContainer}>
        <Button type="button" className={classes.previousButton} onClick={handlePreviousClick}>
          
        </Button>

        <Button className={classes.submitButton} type="submit">
          
        </Button>
      </div>
    </form>
  )
}

export default React.memo(SecondTabVacancy)
