import React from 'react'
import { GridRowId } from '@mui/x-data-grid'
import CourseInfo from '../../../Sub/DataTable/CourseInfo/CourseInfo'
import StatusSelect from '../../../Sub/DataTable/StatusSelect/StatusSelect'
import StudentInfo from '../../../Sub/DataTable/StudentInfo/StudentInfo'
import { TVacancyInfoColumn } from '../../../../services/types/vacacies-page-types/vacancies-page-types'

const baseColumnOptions = {
  sortable: false,
  pinnable: false,
  hideable: false,
}

export const titleIDs: GridRowId[] = ['OFFERED', 'OTHER']

export const getInfoColumns: TVacancyInfoColumn = (statuses, patchStatus) => [
  {
    field: 'id',
    headerName: 'ID',
    width: 85,
    ...baseColumnOptions,
    colSpan: ({ row }) => {
      if (titleIDs.includes(row.id)) {
        return 4
      }
      return undefined
    },
    valueGetter: ({ value, row }) => {
      if (titleIDs.includes(row.id)) {
        return row.title
      }
      return value
    },
  },
  {
    field: 'studentInfo',
    headerName: '',
    width: 186,
    renderCell: (params) => (
      <StudentInfo firstName={params.row?.first_name} lastName={params.row?.last_name} email={params.row?.email} />
    ),
    ...baseColumnOptions,
  },
  {
    field: 'course',
    headerName: '',
    width: 140,
    renderCell: (params) => <CourseInfo course={params?.row?.course} />,
    ...baseColumnOptions,
  },
  {
    field: 'status',
    headerName: '',
    width: 221,
    renderCell: (params) => (
      <StatusSelect
        activeStatus={params.value?.value}
        onPatch={patchStatus}
        applicationId={params.row?.id}
        statuses={statuses}
        width={188}
      />
    ),
    ...baseColumnOptions,
  },
]
