import * as React from 'react'
import Tooltip from '@mui/material/Tooltip'
import useStyles from './MultipleCoursesInfo.style'

function MultipleCoursesInfo({ directory, values }) {
  const classes = useStyles()
  const courses = directory?.filter((c) => values?.includes(c?.id)).map((c) => c?.name)
  const content = courses?.slice(0, 2)?.join(', ')
  const detail = (
    <ul className={classes.list}>
      {courses?.map((item) => (
        <li className={classes.list__item} key={item}>
          {item}
        </li>
      ))}
    </ul>
  )

  return (
    <Tooltip
      title={detail}
      classes={{ tooltip: classes.tooltip }}
      enterDelay={500}
      leaveDelay={200}
      placement="right-end"
    >
      <div className={classes.root}>{content}</div>
    </Tooltip>
  )
}

export default React.memo(MultipleCoursesInfo)
