import React, { useEffect, useState } from 'react'
import { IconButton } from '@material-ui/core'
import Dialog from '@mui/material/Dialog'
import DialogTitle from '@mui/material/DialogTitle'
import DialogContent from '@mui/material/DialogContent'
import useStyles from './VacancyPopup.styles'
import IconClose from '../../../../assets/Icons/IconClose/IconClose'
import Tabs from '../../../Sub/FormElements/vacancy-tabs/vacancy-tabs'
import Tab from '../../../Sub/FormElements/vacancy-tab/vacancy-tab'
import TabPanel from '../../../Sub/FormElements/tab-panel/tab-panel'
import FirstTabVacancy from './FirstTabVacancy'
import SecondTabVacancy from './SecondTabVacancy'
import ThirdTabVacancy from './ThirdTabVacancy'
import FourthTabVacancy from './FourthTabVacancy'
import { useAppSelector } from '../../../../services/store/hooks'
import FifthTabVacancy from './FifthTabVacancy'
import useTabsForm from './useTabsForm'

const initialFormData = {
  tabFirst: {},
  tabSecond: {},
  tabThird: {},
  tabFourth: {},
  tabFifth: {},
}

function VacancyPopup({ openState = [], setSnack }) {
  const [open, setOpen] = openState
  const { directoriesData, vacancyToEdit } = useAppSelector((store) => store.vacanciesPage)
  const [currentTab, setCurrentTab] = useState(0)
  const [formData, setFormData] = useState(initialFormData)
  const [title, setTitle] = useState('')
  const [saveButtonTitle, setSaveButtonTitle] = useState('')
  const classes = useStyles()

  const { validationError, ...hookFormMethods } = useTabsForm(currentTab, open)

  /*  ,        
  const handleTabChange = useCallback((event, newValue) => {
    setCurrentTab(newValue)
  }, [])
  */

  const handleClose = () => {
    setOpen(false)
    setCurrentTab(0)
    setFormData(initialFormData)
  }

  useEffect(() => {
    if (open) {
      setTitle(vacancyToEdit ? ' ' : ' ')
      setSaveButtonTitle(vacancyToEdit ? '' : '')
    }
    if (open && vacancyToEdit)
      setFormData({
        tabFirst: {
          company_name: vacancyToEdit.company_name,
          website: vacancyToEdit.website,
          position: vacancyToEdit.position,
          courses: vacancyToEdit.courses,
          isSalaryNotRequired: !vacancyToEdit.salary && !vacancyToEdit.salary_max && !vacancyToEdit.currency,
          salary: vacancyToEdit.salary,
          salary_max: vacancyToEdit.salary_max,
          currency: vacancyToEdit.currency,
          isConfidentialLinkNotRequired: !vacancyToEdit.company_confidential_url,
          company_confidential_url: vacancyToEdit.company_confidential_url,
        },
        tabSecond: {
          cities: vacancyToEdit.cities,
          employment: vacancyToEdit.employment,
          design_formats: vacancyToEdit.design_formats,
          work_format: vacancyToEdit.work_format,
          experience_type: vacancyToEdit.experience_type,
          education_type: vacancyToEdit.education_type,
          isTestTaskLinkNotRequired: !vacancyToEdit.test_task_url,
          test_task_url: vacancyToEdit.test_task_url,
        },
        tabThird: {
          vacancy_info: vacancyToEdit.vacancy_info,
          responsibilities: vacancyToEdit.responsibilities,
          circs: vacancyToEdit.circs,
        },
        tabFourth: {
          mandatory_requirements: vacancyToEdit.mandatory_requirements,
          optional_requirements: vacancyToEdit.optional_requirements,
          selection_stages: vacancyToEdit.selection_stages,
          chat_id: vacancyToEdit.chat_id,
          vacancy_url: vacancyToEdit.vacancy_url,
        },
        tabFifth: {
          company_logo: vacancyToEdit.company_logo,
          small_cover: vacancyToEdit.small_cover,
          big_cover: vacancyToEdit.big_cover,
        },
      })
    if (!open) hookFormMethods.reset({})
  }, [open])

  return (
    <div>
      <Dialog
        maxWidth="xl"
        open={open}
        onClose={handleClose}
        disableScrollLock
        className={classes.root}
        sx={
          vacancyToEdit && {
            '.MuiBackdrop-root': {
              backgroundColor: 'transparent',
            },
          }
        }
      >
        <IconButton onClick={handleClose} className={classes.closePopup}>
          <IconClose />
        </IconButton>
        <DialogTitle className={classes.dialogTitle}>{title}</DialogTitle>
        <DialogContent className={classes.dialogContent}>
          <Tabs value={currentTab}>
            {/* onChange={handleTabChange} */}
            <Tab label="1.  " disabled />
            <Tab label="2. " disabled />
            <Tab label="3. " disabled />
            <Tab label="4.   " disabled />
            <Tab label="5. " disabled />
          </Tabs>
          <TabPanel value={currentTab} index={0}>
            <FirstTabVacancy
              currencies={directoriesData?.currencies}
              courses={directoriesData?.courses}
              setCurrentTab={setCurrentTab}
              formState={[formData, setFormData]}
              isEdit={!!vacancyToEdit}
              handleClose={handleClose}
              hookFormMethods={hookFormMethods}
            />
          </TabPanel>
          <TabPanel value={currentTab} index={1}>
            <SecondTabVacancy
              directories={directoriesData}
              setCurrentTab={setCurrentTab}
              formState={[formData, setFormData]}
              hookFormMethods={hookFormMethods}
            />
          </TabPanel>
          <TabPanel value={currentTab} index={2}>
            <ThirdTabVacancy
              setCurrentTab={setCurrentTab}
              formState={[formData, setFormData]}
              hookFormMethods={hookFormMethods}
            />
          </TabPanel>
          <TabPanel value={currentTab} index={3}>
            <FourthTabVacancy
              setCurrentTab={setCurrentTab}
              formState={[formData, setFormData]}
              hookFormMethods={hookFormMethods}
            />
          </TabPanel>
          <TabPanel value={currentTab} index={4}>
            <FifthTabVacancy
              setCurrentTab={setCurrentTab}
              formState={[formData, setFormData]}
              setSnack={setSnack}
              handleClose={handleClose}
              saveButtonTitle={saveButtonTitle}
              hookFormMethods={hookFormMethods}
              validationError={validationError}
            />
          </TabPanel>
        </DialogContent>
        {/* <DialogActions className={classes.dialogActions}>
          <Button form="form-edit-profile" className={classes.submitButton} type="submit">
            
          </Button>
        </DialogActions> */}
      </Dialog>
    </div>
  )
}

export default React.memo(VacancyPopup)
