import React, { useEffect, useState } from 'react'
import Button from '@material-ui/core/Button'
import useStyles from './VacancyPopup.styles'
import ControlledTextField from '../../../Sub/FormElements/controlled-text-field/controlled-text-field'

function ThirdTabVacancy({ setCurrentTab, formState, hookFormMethods }) {
  const [formData, setFormData] = formState
  const classes = useStyles()
  const [countAboutCompanyText, setCountAboutCompanyText] = useState(0)
  const [countResponsibilityText, setCountResponsibilityText] = useState(0)
  const [countConditionsText, setCountConditionsText] = useState(0)

  const { control, register, watch, getValues, handleSubmit, errors } = hookFormMethods

  const aboutCompanyText = watch('vacancy_info')
  const responsibilitiesText = watch('responsibilities')
  const conditionsText = watch('circs')

  useEffect(() => {
    register('vacancy_info')
    register('responsibilities')
    register('circs')
  }, [register])

  useEffect(() => {
    setCountAboutCompanyText(aboutCompanyText ? aboutCompanyText.length : 0)
  }, [aboutCompanyText])

  useEffect(() => {
    setCountResponsibilityText(responsibilitiesText ? responsibilitiesText.length : 0)
  }, [responsibilitiesText])

  useEffect(() => {
    setCountConditionsText(conditionsText ? conditionsText.length : 0)
  }, [conditionsText])

  const handlePreviousClick = () => {
    const values = getValues()
    setFormData((prev) => ({ ...prev, tabThird: values }))
    setCurrentTab(1)
  }
  const onSubmit = (data) => {
    setFormData((prev) => ({ ...prev, tabThird: data }))
    setCurrentTab(3)
  }

  return (
    <form className={classes.form} onSubmit={handleSubmit(onSubmit)}>
      <div className={classes.fieldsGridFlex}>
        <ControlledTextField
          labelTitle="    "
          fieldName="vacancy_info"
          control={control}
          error={!!errors.vacancy_info?.message}
          helperText={errors.vacancy_info?.message}
          placeholder=" "
          multiline
          rows={3}
          symbolsCount={countAboutCompanyText}
          maxLength={1000}
          minLength={20}
          maxWidth={632}
          textClass={classes.textAreaLarge}
        />
        <div className={classes.fieldsContainer}>
          <ControlledTextField
            labelTitle=""
            fieldName="responsibilities"
            control={control}
            error={!!errors.responsibilities?.message}
            helperText={errors.responsibilities?.message}
            placeholder=" "
            multiline
            rows={4}
            symbolsCount={countResponsibilityText}
            maxLength={1000}
            minLength={20}
            maxWidth={306}
            textClass={classes.textAreaSmall}
          />
          <ControlledTextField
            labelTitle=""
            fieldName="circs"
            control={control}
            error={!!errors.circs?.message}
            helperText={errors.circs?.message}
            placeholder=" "
            multiline
            rows={4}
            symbolsCount={countConditionsText}
            maxLength={1000}
            minLength={20}
            maxWidth={306}
            textClass={classes.textAreaSmall}
          />
        </div>
      </div>
      <div className={classes.buttonContainer}>
        <Button type="button" className={classes.previousButton} onClick={handlePreviousClick}>
          
        </Button>
        <Button className={classes.submitButton} type="submit">
          
        </Button>
      </div>
    </form>
  )
}

export default React.memo(ThirdTabVacancy)
