import React from 'react'
import CourseInfo from '../../../components/Sub/DataTable/CourseInfo/CourseInfo'
import MultipleCoursesInfo from '../../../components/Sub/DataTable/MultipleCoursesInfo/MultipleCoursesInfo'
import StatusSelect from '../../../components/Sub/DataTable/StatusSelect/StatusSelect'
import { formatRusStyle } from '../../../utils/dates'
import { TGetVacanciesColumns } from '../../../services/types/vacacies-page-types/vacancies-page-types'

export const getVacanciesColumns: TGetVacanciesColumns = (patchStatus, courses, vacancyStatuses) => [
  { field: 'id', headerName: 'ID', width: 44 },
  {
    field: 'company_name',
    headerName: '',
    width: 168,
    renderCell: (params) => <CourseInfo course={params.value} />,
  },
  {
    field: 'position',
    headerName: '',
    width: 168,
    renderCell: (params) => <CourseInfo course={params.value} />,
  },

  {
    field: 'courses',
    headerName: '',
    width: 130,
    renderCell: (params) => <MultipleCoursesInfo directory={courses} values={params.value} />,
    sortable: false,
  },
  {
    field: 'response_count',
    headerName: '- ',
    align: 'left',
    width: 130,
    sortable: false,
  },
  {
    field: 'find_work_count',
    headerName: ' ',
    align: 'left',
    width: 116,
    sortable: false,
  },
  {
    field: 'start_date',
    headerName: ' ',
    type: 'string',
    align: 'left',
    width: 130,
    valueFormatter: (params) => params.value && formatRusStyle(params.value),
  },
  {
    field: 'end_date',
    headerName: ' ',
    type: 'string',
    align: 'left',
    width: 123,
    valueFormatter: (params) => params.value && formatRusStyle(params.value),
  },
  {
    field: 'vacancy_status',
    headerName: '',
    width: 180,
    renderCell: (params) => (
      <StatusSelect
        activeStatus={params.value}
        onPatch={patchStatus}
        applicationId={params.row?.id}
        statuses={vacancyStatuses}
        width={180}
      />
    ),
  },
]

export default getVacanciesColumns
