import React, { FC, useEffect, useState } from 'react'
import qs from 'qs'
import { GridCellParams } from '@mui/x-data-grid'
import DataTable from '../../../components/Sub/DataTable/DataTableWithServerPagination'
import Filters from '../../../components/Sub/Filters/Filters'
import VacancyPopup from '../../../components/Main/VacanciesListPage/VacancyPopup/VacancyPopup'
import InfoPopup from './InfoPopup/InfoPopup'
import { getVacanciesColumns } from './columns'
import useFilters from '../../../hooks/useFilters'
import useStyles from './VacanciesListPage.style'
import VacancyInfoPopup from '../../../components/Main/VacanciesListPage/VacancyInfoPopup/VacancyInfoPopup'
import { FoundJobPopup } from '../../../components/Sub/GlobalPopups'
import { IParams, IStatus, IVacanciesPageList } from '../../../services/types/vacacies-page-types/vacancies-page-types'
import { useAppDispatch, useAppSelector } from '../../../services/store/hooks'
import { getDirectories, getVacanciesList, patchVacancyStatus } from '../../../services/axios/vacancies-page'
import {
  resetUpdateStatus,
  resetUpdateStudentStatus,
  setIsInfoPopupOpen,
  setIsOpenFoundJobPopup,
  setIsOpenVacancyPopup,
  setVacancyToArchive,
  setVacancyToEdit,
} from '../../../services/slices/vacancies-page-slice'

const sortFieldsMapping = {
  id: 'id',
  company_name: 'company_name',
  position: 'position',
  start_date: 'start_date',
  end_date: 'end_date',
  vacancy_status: 'vacancy_status_id',
}

const VacanciesListPage: FC<IVacanciesPageList> = ({ setSnack }) => {
  const classes = useStyles()
  const hook = useFilters()
  const dispatch = useAppDispatch()
  const {
    filtersJSON,
    directoriesData,
    isOpenVacancyPopup,
    vacancyToEdit,
    vacancies,
    isLoading,
    isOpenFoundJobPopup,
    studentToEdit,
    updateStatus,
    updateStudentStatus,
    rowsCount,
  } = useAppSelector((store) => store.vacanciesPage)

  const paramsSerializer = (params: IParams) => qs.stringify(params, { arrayFormat: 'repeat' })

  const getVacanciesListOnPage = async () => {
    const data = { hook, paramsSerializer }
    try {
      await dispatch(getVacanciesList(data))
    } catch {
      return undefined
    }
    return undefined
  }

  const getDirectoriesOnPage = async () => {
    try {
      await dispatch(getDirectories())
    } catch {
      return undefined
    }
    return undefined
  }

  const patchVacancyStatusOnPage = async (vacancyId: number | null, status: IStatus) => {
    const patchData = { setSnack, vacancyId, status }
    try {
      await dispatch(patchVacancyStatus(patchData))
    } catch {
      return undefined
    }
    return undefined
  }

  const patchStatus = (vacancyId: number, status: IStatus) => {
    if (status?.status === '') dispatch(setVacancyToArchive(vacancyId))
    else patchVacancyStatusOnPage(vacancyId, status)
  }

  const handleVacancyClick = (params: GridCellParams) => {
    dispatch(setVacancyToEdit(params?.row))
    dispatch(setIsInfoPopupOpen(true))
  }

  const columns = getVacanciesColumns(patchStatus, directoriesData?.courses, directoriesData?.statuses)

  useEffect(() => {
    getDirectoriesOnPage()
  }, [])

  useEffect(() => {
    if (hook.filters.start_date && !isOpenVacancyPopup) getVacanciesListOnPage()
  }, [hook.filters, isOpenVacancyPopup])

  useEffect(() => {
    if (updateStatus === 'ok') {
      setSnack({
        open: true,
        header: ' ',
        message: '      ',
      })
      dispatch(resetUpdateStatus())
    }
  }, [updateStatus])

  useEffect(() => {
    //   ,    
    if (updateStudentStatus) {
      getVacanciesListOnPage()
      dispatch(resetUpdateStudentStatus())
    }
  }, [updateStudentStatus])

  return (
    <main className={classes.root}>
      <Filters
        filterType="vacancy"
        hook={hook}
        filtersJSON={filtersJSON}
        handleAddVacancy={() => dispatch(setIsOpenVacancyPopup(true))}
        handleDownload={undefined}
        handleFeedExport={undefined}
        handleFunnelExport={undefined}
        handleAddCohort={undefined}
        handleAddContact={undefined}
        contactsCount={undefined}
        isStatistics={undefined}
      />
      <VacancyPopup
        openState={[isOpenVacancyPopup, (status: boolean) => dispatch(setIsOpenVacancyPopup(status))]}
        setSnack={setSnack}
      />
      <VacancyInfoPopup />
      <FoundJobPopup
        setPrevPopup={(status: boolean) => dispatch(setIsInfoPopupOpen(status))}
        setSnack={setSnack}
        openState={[isOpenFoundJobPopup, (status: boolean) => dispatch(setIsOpenFoundJobPopup(status))]}
        vacancy={vacancyToEdit}
        studentToEdit={studentToEdit}
        updateVacancies={getVacanciesListOnPage}
      />
      <InfoPopup
        title="  "
        subtitle="    ,        "
      />
      <DataTable
        onCellClick={handleVacancyClick}
        sx={{
          '& .MuiDataGrid-cell': { cursor: 'pointer' },
        }}
        columns={columns}
        rows={vacancies}
        loading={isLoading}
        filtersHook={hook}
        rowsCount={rowsCount}
        sortFieldsMapping={sortFieldsMapping}
      />
    </main>
  )
}

export default React.memo(VacanciesListPage)
